<template>
  <div class="page">
    <!-- <div class="title">
      <img class="pic" src="@/assets/img/title.png" alt />
    </div>-->
    <!-- <mxTitle /> -->
    <mx-titles text='"第十八届中国日报社社“21世纪杯”小学生英语演讲大会暨第四届少儿英语大会"'></mx-titles>
    <div class="group">
      <el-form ref="form" :model="form" :rules="rules">
        <!-- 参赛人姓名 -->
        <el-form-item prop="name">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f1.png" alt />
            </div>
            <el-input v-model="form.name" placeholder="请填写参赛人姓名"></el-input>
          </div>
        </el-form-item>
        <!-- 参赛编号 -->
        <el-form-item prop="serial">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f2.png" alt />
            </div>
            <el-input v-model="form.serial" placeholder="请填写参赛编号(欧文或学校反馈的电话号码)"></el-input>
          </div>
        </el-form-item>
        <!-- 就读年级 -->
        <el-form-item prop="grade">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f3.png" alt />
            </div>
            <el-select v-model="form.grade" placeholder="下拉选择就读年级">
              <el-option
                v-for="grade in form.grades"
                :key="grade.value"
                :label="grade.label"
                :value="grade.value"
              ></el-option>
            </el-select>
          </div>
        </el-form-item>

        <!-- 参赛类别 -->
        <el-form-item prop="sort">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f4.png" alt />
            </div>
            <el-input v-model="form.sort" placeholder="根据年级自动选择参赛类别"></el-input>
          </div>
        </el-form-item>

        <!-- 个人照片 -->
        <el-form-item prop="mePhoto">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f5.png" />
            </div>
            <div class="photo">
              <!-- 添加的图片样式 -->
              <!-- <div class="pic"></div> -->
              <!-- <div class="add" @click="uploadMePhoto">+</div> -->
              <el-upload
                action="https://jsonplaceholder.typicode.com/posts/"
                list-type="picture-card"
                :show-file-list="false"
                :on-preview="(file) => handlePictureCardPreview(file, 'mePhoto')"
                :on-remove="handleRemove"
                :before-upload="beforeAvatarUpload"
              >
                <el-img v-if="form.mePhotoUrl" :src="form.mePhotoUrl" class="avatar"></el-img>
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
            <div class="tip">提示：照片大小不大于20M</div>
          </div>
        </el-form-item>

        <!-- 封面照片 -->
        <el-form-item prop="coverPhoto" v-if="types==='auth'">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f8.png" />
            </div>
            <div class="photo post">
              <!-- 添加的图片样式 -->
              <!-- <div class="pic"></div> -->
              <!-- <div class="add">+</div> -->
              <el-upload
                action="https://jsonplaceholder.typicode.com/posts/"
                list-type="picture-card"
                :on-preview="(file) => handlePictureCardPreview(file, 'coverPhoto')"
                :on-remove="handleRemove"
                :before-upload="beforeAvatarUpload"
              >
                <el-img v-if="form.coverPhotoUrl" :src="form.coverPhotoUrl" class="avatar"></el-img>
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
            <div class="tip">提示：照片玻璃16:9,照片大小不大于20M</div>
          </div>
        </el-form-item>

        <!-- 上传视频 -->
        <el-form-item prop="uploadVideo" v-if="types==='auth'">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f9.png" />
            </div>
            <el-upload
              class="upload-demo"
              action="https://jsonplaceholder.typicode.com/posts/"
            >
              <div class="btn upload">
                <div class="word">上传视频</div>
                <img class="icon" src="@/assets/img/ic_ul.png" />
              </div>
            </el-upload>
            <div class="tip">视频要求：视频格式,不大于MB,视频时长两分钟</div>
          </div>
        </el-form-item>

        <!-- 家长电话 -->
        <el-form-item prop="patriarchPhone">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f6.png" />
            </div>
            <el-input v-model="form.phone" type="number" placeholder="请填写家长电话"></el-input>
          </div>
        </el-form-item>

        <!-- 验证码 -->
        <el-form-item prop="verificationCode">
          <div class="item">
            <div class="wrap">
              <img class="pic" src="@/assets/img/f7.png" />
            </div>
            <el-input v-model="form.code" type="number" placeholder="请填写验证码">
              <el-button slot="append" @click="getCode" :disabled="codeDisabled">{{codeMsg}}</el-button>
            </el-input>
          </div>
        </el-form-item>

        <!-- 提交按钮 -->
        <div class="submit" @click="submit('form')">{{types==='auth'?'立即报名':'立即认证'}}</div>
      </el-form>
    </div>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" />
    </el-dialog>
  </div>
</template>

<script>
import mxTitles from '@/components/basic/title.vue'
export default {
  data () {
    return {
      dialogImageUrl: '',
      imageUrl: '',
      dialogVisible: false,
      form: {
        // 姓名
        name: '',
        //  编号
        serial: '',
        //  年级
        grades: [{ value: 1, label: '一年级' }, { value: 2, label: '二年级' }],
        grade: [],
        // 组别
        sort: '',
        // 个人照片url
        mePhotoUrl: '',
        // 封面照片url
        coverPhotoUrl: '',
        // 家长电话
        phone: '',
        // 验证码
        code: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入参赛人姓名', trigger: 'blur' }
        ],
        serial: [
          { required: true, message: '请输入参赛编号', trigger: 'blur' }
        ],
        grade: [
          {
            type: 'array',
            required: true,
            message: '请选择就读年级',
            trigger: 'blur'
          }
        ],
        sort: [
          {
            type: 'array',
            required: true,
            message: '请选择参赛类别',
            trigger: 'blur'
          }
        ],
        mePhoto: [
          { required: true, message: '请上传个人图片', trigger: 'blur' }
        ],
        coverPhoto: [
          { required: true, message: '请上传封面照片', trigger: 'blur' }
        ],
        uploadVideo: [
          { required: true, message: '请上传视频', trigger: 'blur' }
        ],
        patriarchPhone: [
          { required: true, message: '请输入家长电话', trigger: 'blur' }
        ],
        verificationCode: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
        ]
      },
      // 是否禁用按钮
      codeDisabled: false,
      // 倒计时秒数
      countdown: 60,
      // 按钮上的文字
      codeMsg: '获取验证码',
      // 定时器
      timer: null
    }
  },
  props: {
    types: {
      type: String,
      default: 'apply'
    }
  },
  methods: {
    beforeAvatarUpload (file) {
      // const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 20

      // if (!isJPG) {
      //   this.$message.error('上传头像图片只能是 JPG 格式!')
      // }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // return isJPG && isLt2M
      return isLt2M
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePictureCardPreview (file, typeName) {
      console.log(file, typeName, 'file')
      this.imageUrl = file.url
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    // 上传个人照片
    uploadMePhoto () {},
    submit (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          this.$message({
            type: 'warning',
            message: '请填写完整信息'
          })
          return false
        }
      })
    },
    // 获取验证码
    getCode () {
      // 验证码60秒倒计时
      if (!this.timer) {
        this.timer = setInterval(() => {
          if (this.countdown > 0 && this.countdown <= 60) {
            this.countdown--
            if (this.countdown !== 0) {
              this.codeMsg = '重新发送(' + this.countdown + ')'
              this.codeDisabled = true
            } else {
              clearInterval(this.timer)
              this.codeMsg = '获取验证码'
              this.countdown = 60
              this.timer = null
              this.codeDisabled = false
            }
          }
        }, 1000)
      }
    }
  },
  components: {
    mxTitles
  }
}
</script>

<style lang="less" scoped>
.el-select-dropdown__item.selected {
  color: #ff5701;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.page {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5vw 0;
  // .title {
  //   text-align: center;
  //   padding: 0 5vw;
  //   .pic {
  //     width: 100%;
  //   }
  // }
  .group {
    width: 100%;
    margin-top: 5vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    .submit {
      width: 87vw;
      height: 11vw;
      background-color: #ff5701;
      border-radius: 5vw;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 2vw;
    }
    /deep/.item {
      .el-input__inner {
        width: 100%;
        background-color: #f8f8f8;
        border: solid 1px #e5e5e5;
        padding-left: 5vw;
        font-size: 12px;
      }
    }
    .item {
      width: 77vw;
      display: flex;
      flex-direction: column;
      margin-bottom: 5vw;
      position: relative;
      .upload {
        width: 25vw;
        height: 8vw;
        background-color: #ff5701;
        border-radius: 1vw;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        .word {
          color: #fff;
          font-size: 12px;
          margin-right: 10px;
        }
        .icon {
          width: 12px;
        }
      }
      .tip {
        color: #222;
        font-size: 12px;
        margin-top: 2vw;
      }
      .wrap {
        margin-bottom: 2vw;
        .pic {
          height: 3vw;
        }
      }
      .photo {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        &.post {
          .add {
            width: 40vw;
            height: 30vw;
          }
          .pic {
            width: 40vw;
            height: 30vw;
          }
        }
        .add {
          width: 30vw;
          height: 40vw;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: #f8f8f8;
          border: 1px solid #e5e5e5;
          color: #999;
          font-size: 4rem;
          font-weight: 100;
          margin-right: 2vw;
        }
        .pic {
          width: 30vw;
          height: 40vw;
        }
      }
    }
  }
}
</style>



